import { Component, EventEmitter, Inject, Input, OnInit, Output, SimpleChanges, SkipSelf } from "@angular/core";
import { ErrorStateMatcher } from "@angular/material/core";
import { MatLegacyDialog as MatDialog, MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA } from "@angular/material/legacy-dialog";
import { AbstractFormGroupControlValueAccessor, ngFormGroupControlValueAccessorProviders } from "@tei/core";
import { ConfirmDialogService, DialogData, SnackBarService } from "@tei/material";
import { AvailableCashEq, GetCashEqRedemptionRequest, GetCashEqRedemptionResponse, GetVoucherDetailsRequest, ICashEqRedemptionService, PostCashEqRedemptionRequest, PostCashEqRedemptionRequestForm } from "../../@intermediate/cash-eq-redemption";
import { VoucherDetailDialogComponent } from "../voucher-detail-dialog/voucher-detail-dialog.component";
import { TranslateService } from "@ngx-translate/core";

@Component({
  selector: "tei-payment-engine-shared-cash-eq-redemption",
  templateUrl: "./cash-eq-redemption.component.html",
  styleUrls: ["./cash-eq-redemption.component.scss"],
  providers: [
    ngFormGroupControlValueAccessorProviders(CashEqRedemptionComponent),
  ],
})
export class CashEqRedemptionComponent
  extends AbstractFormGroupControlValueAccessor
  implements OnInit
{
  model: GetCashEqRedemptionResponse;
  form: PostCashEqRedemptionRequestForm;
  @Output() applyRedemption = new EventEmitter<any>();
  @Input() cashEqAppliedBalance: PostCashEqRedemptionRequest;
  @Input() shoppingCartBalance: number;
  @Input() numberOfRedeemableItems: number;
  public Items: { ItemID: string; Amount: number; }[] = [];
  public cashEqBalance: number;
  public cashEqBalanceApplied: boolean = false;
  public redemptionError: boolean = true;

  constructor(
    @SkipSelf() protected _errorStateMatcher: ErrorStateMatcher,
    @Inject("ICashEqRedemptionService")
    private CashEqRedemptionService: ICashEqRedemptionService,
    private snackbarService: SnackBarService,
    private confirmdialogService: ConfirmDialogService,
    public dialog: MatDialog,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private teiTextTranslateService: TranslateService,
  )
  {
    super(_errorStateMatcher);
  }

  ngOnInit()
  {
    this.form = new PostCashEqRedemptionRequestForm();
    const request = new GetCashEqRedemptionRequest();
    this.load(request);
  }

  ngOnChanges(changes: SimpleChanges)
  {
    if (this.cashEqAppliedBalance != null)
    {
      const cashEqItemUID = this.cashEqAppliedBalance.CashEqItemUID;
      let c = this.model.AvailableCashEqs.find((i) => i.CashEqItemUID === cashEqItemUID);
      if (this.cashEqAppliedBalance.IsRemoved)
      {
        c.OnlineStatus = -1;
        c.AppliedAmount = 0;
      }
      else
      {
        this.cashEqBalanceApplied = true;
        c.OnlineStatus = 0;
        c.TrxUID = this.cashEqAppliedBalance.TrxUID;
        if (this.cashEqAppliedBalance.ReApply)
        {
          c.Amount = this.cashEqAppliedBalance.Amount;
        }
        c.AppliedAmount = this.cashEqAppliedBalance.TotalAppliedAmount;
      }
    }

  }




  private load(request: GetCashEqRedemptionRequest)
  {
    if (!this.model)
    {
      this.model = new GetCashEqRedemptionResponse();
    }

    this.CashEqRedemptionService.Get(request).subscribe((response) =>
    {
      this.model = response;
      this.writeValue(this.model);
    });
  }

  apply(cashEqItemUID, amount)
  {
    const existing = this.Items.find((i) => i.ItemID === cashEqItemUID);
    const existingCashEQ = this.model.AvailableCashEqs.find(
      (i) => i.CashEqItemUID === cashEqItemUID
    );

    let req = new PostCashEqRedemptionRequest();
    const trxType = this.model.AvailableCashEqs.find((i) => i.CashEqItemUID === cashEqItemUID);
    req.RedeemCashEq = false;
    req.ClaimCode = null;
    req.CashEqItemUID = cashEqItemUID;
    req.OrigAmount = amount;
    req.IsRemoved = trxType.OnlineStatus == 0 ? true : false;
    req.TrxUID = trxType.TrxUID;
    req.TotalAppliedAmount = trxType.AppliedAmount;
    req.CashEqID = trxType.CashEqID;
    req.Name = trxType.Name;
    req.Description = trxType.Description;
    req.TypeID = Number(trxType.Type);
    if (req.TypeID === 1)
    {
      if (req.IsRemoved)
      {
        this.numberOfRedeemableItems = this.numberOfRedeemableItems + 1;
      }
      else
      {
        this.numberOfRedeemableItems = this.numberOfRedeemableItems - 1;
      }

    }
    this.applyRedemption.emit(req);
  }

  redeemCashEq(claimId: string)
  {
    if (claimId.trim())
    {
      let req = new PostCashEqRedemptionRequest();
      req.RedeemCashEq = true;
      req.ClaimCode = claimId;
      this.CashEqRedemptionService.Post(req).subscribe(response =>
      {
        this.model = response.AvailableCashEqs;
        this.snackbarService.showSuccess(response.AvailableCashEqs.SuccessMessage);
      },
        error =>
        {
          this.form.controls["ClaimCode"].setErrors({ cliamCodeInvalid: true, message: error.error.Message });
        });
    }
  }

  cashEqUnAvailable(m: AvailableCashEq)
  {
    if (!m.RedeemOnline)
    {
      const dialogData: DialogData = {
        title: this.teiTextTranslateService.instant('PAYMENT_ENGINE.CASH_EQ_REDEMPTION.UNAVAILABLE'),
        message: this.teiTextTranslateService.instant('PAYMENT_ENGINE.CASH_EQ_REDEMPTION.UNAVAILABLE_MESSAGE'),
        textOkayButton: this.teiTextTranslateService.instant('PAYMENT_ENGINE.CASH_EQ_REDEMPTION.CLOSE'),
        textCancelButton: ''
      };
      this.confirmdialogService.openDialog(dialogData, { disableClose: true });
    }
  }

  OnVoucherDetail(uid: string)
  {
    this.dialog.closeAll();
    let request = new GetVoucherDetailsRequest();
    request.CertificateID = uid;
    this.CashEqRedemptionService.GetVoucherDetails(request).subscribe(result =>
    {
      const res = result.VoucherDetailInfo;
      const Course = result.CourseInfo;
      const trans = result.TransactionDetailsInfo;
      this.dialog.open(VoucherDetailDialogComponent, {
        width: '400px',
        height: '600px',
        panelClass: 'o-dialog-sm-fullscreen',
        data: {
          Name: res.Description,
          Description: res.Description,
          CashEqID: res.CashEqID,
          CertID: res.CertID,
          IssuedDate: res.IssuedDate,
          ExpiryDate: res.ExpiryDate,
          OriginalBalance: res.OriginalBalance,
          CurrentBalance: res.CurrentBalance,
          IsPromo: res.IsPromo,
          CertNumRounds: res.CertNumRounds,
          MinPlayersRequired: res.MinPlayersRequired,
          numPlayers: res.numPlayers,
          CertType: res.CertType,
          RedeemableAt: res.RedeemableAt,
          RemainingRound: res.RemainingRound,
          CertificateID: res.CertificateID,
          CourseNames: Course,
          Transactions: trans
        },
      });

    });
  }


}
