import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetCashEqRedemptionRequest, GetCashEqRedemptionResponse, GetVoucherDetailsRequest, GetVoucherDetailsResponse, ICashEqRedemptionService, PostCashEqRedemptionRequest, PostCashEqRedemptionResponse } from '../@intermediate/cash-eq-redemption';

@Injectable({
  providedIn: 'root'
})

export class CashEqRedemptionService implements ICashEqRedemptionService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/PaymentEngine/CashEqRedemption';
  }

  public Get(request: GetCashEqRedemptionRequest): Observable<GetCashEqRedemptionResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetCashEqRedemptionResponse>(apiUrl, { params: request as any });
  }

  public GetVoucherDetails(request: GetVoucherDetailsRequest): Observable<GetVoucherDetailsResponse>
  {
    const apiUrl = this.serviceUrl + '/GetVoucherDetails';
    return this.http.get<GetVoucherDetailsResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostCashEqRedemptionRequest): Observable<PostCashEqRedemptionResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostCashEqRedemptionResponse>(apiUrl, request);
  }
}
