import { FormControl, UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { ECheckAccount } from './echeck-account.model';


export class PostManageECheckAccountRequest
{
  AccountName: string;
  AccountType: number;
  FirstName: string;
  LastName: string;
  CompanyName: string;
  BankRoutingNumber: string;
  BankAccountNumber: string;
  AccountID: string | null;
}

export class PostManageECheckAccountRequestForm extends UntypedFormGroup
{
  constructor()
  {
    super({
      AccountName: new UntypedFormControl('', Validators.required),
      AccountType: new UntypedFormControl('', Validators.required),
      FirstName: new UntypedFormControl('', Validators.required),
      LastName: new UntypedFormControl('', Validators.required),
      CompanyName: new UntypedFormControl('', Validators.required),
      BankRoutingNumber: new UntypedFormControl('', Validators.compose([Validators.min(10000000), Validators.max(999999999), Validators.minLength(9), Validators.maxLength(9), Validators.required])),
      BankAccountNumber: new UntypedFormControl('', Validators.compose([Validators.maxLength(20), Validators.required])),
      AccountID: new FormControl<string>('')
    });
  }

  public value: PostManageECheckAccountRequest;

}

export class PostManageECheckAccountResponse 
{

  ResponseStatus: string;
  ResponseMessage: string;
  ECheckAccount: ECheckAccount;
}
