import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetViewPickupLocationRequest, GetViewPickupLocationResponse, IViewPickupLocationService } from '../@intermediate/view-pickup-location';

@Injectable({
  providedIn: 'root'
})

export class ViewPickupLocationService implements IViewPickupLocationService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/PaymentEngine/ViewPickupLocation';
  }

  public Get(request: GetViewPickupLocationRequest): Observable<GetViewPickupLocationResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetViewPickupLocationResponse>(apiUrl, { params: request as any });
  }
}
