import { UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { GetCashEqRedemptionResponse } from './get-cash-eq-redemption.model';

export class PostCashEqRedemptionRequest
{
  public RedeemCashEq: boolean;
  public ClaimCode: string;
  public IsRemoved: boolean;
  public CashEqItemUID: string;
  public Amount: number;
  public TotalAppliedAmount: number;
  public OrigAmount: number;
  public TrxUID: string;
  public Name: string;
  public Description: string;
  public CashEqID: string;
  public ReApply: boolean;
  public RemoveAllCashEq: boolean;
  public TypeID: number;
}

export class PostCashEqRedemptionRequestForm extends UntypedFormGroup
{
  constructor()
  {
    super({
      ClaimCode: new UntypedFormControl(''),
    });
  }

  public value: PostCashEqRedemptionRequest;

  public setValue(
    value: GetCashEqRedemptionResponse,
    options?: {
      onlySelf?: boolean;
      emitEvent?: boolean;
    }): void
  {
    const request = new PostCashEqRedemptionRequest();
    request.ClaimCode = "";
    super.setValue(request, options);
  }
}

export class PostCashEqRedemptionResponse
{
  public AvailableCashEqs: GetCashEqRedemptionResponse;
  public Message: string;
  public AppliedBalance: number;
}

export class AppliedCashEq 
{
  public CashEQItemUID: string;
  public AppliedAmount: number;
  public TrxUID: string;
  public InitialAmount: number;
}