import { Component, OnInit, Inject, forwardRef, Input } from '@angular/core';
import { ISelectECheckAccountService, GetSelectECheckAccountRequest, GetSelectECheckAccountResponse, SelectedECheckFormGroup, SelectedECheckAccount } from '../../@intermediate/select-echeck-account';
import { Validator, ControlValueAccessor, AbstractControl, ValidationErrors, NG_VALUE_ACCESSOR, NG_VALIDATORS } from '@angular/forms';
import { ECheckAccount } from '../../@intermediate/manage-echeck-account';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { ManageECheckAccountComponent } from '../manage-echeck-account/manage-echeck-account.component';
import { PostSelectECheckAccountRequest } from '../../@intermediate/select-echeck-account/models/post-select-echeck-account.model';
import { SnackBarService, DialogData, ConfirmDialogService } from '@tei/material';
// import { ReplaceSource } from 'webpack-sources';

@Component({
  selector: 'tei-payment-engine-shared-select-echeck-account',
  templateUrl: './select-echeck-account.component.html',
  styleUrls: ['./select-echeck-account.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => SelectECheckAccountComponent),
      multi: true
    },
    {
      provide: NG_VALIDATORS,
      useExisting: forwardRef(() => SelectECheckAccountComponent),
      multi: true
    }
  ]
})
export class SelectECheckAccountComponent implements OnInit, ControlValueAccessor, Validator
{
  model: GetSelectECheckAccountResponse;
  form: SelectedECheckFormGroup;
  newECheckAccount: ECheckAccount = new ECheckAccount();
  @Input() accountID: string = null;

  constructor(@Inject('ISelectECheckAccountService') private SelectECheckAccountService: ISelectECheckAccountService,
    private echeckDialog: MatDialog,
    private snackbarService: SnackBarService,
    private confirmdialogService: ConfirmDialogService)
  {

  }

  ngOnInit()
  {
    this.form = new SelectedECheckFormGroup();
    const request = new GetSelectECheckAccountRequest();
    request.AccountID = this.accountID;
    this.load(request);
  }

  private load(request: GetSelectECheckAccountRequest)
  {
    if (!this.model)
    {
      this.model = new GetSelectECheckAccountResponse();
    }

    this.SelectECheckAccountService.Get(request).subscribe(response =>
    {
      this.model = response;
      if (this.form.value && this.form.value.SelectedECheckAccountDetail && this.form.value.SelectedECheckAccountDetail.RecurringID)
      {
        const acc: SelectedECheckAccount = { SelectedECheckAccountDetail: this.model.ECheckAccounts.find(cc => cc.RecurringID === this.form.value.SelectedECheckAccountDetail.RecurringID) };
        if (acc !== undefined)
        {
          this.writeValue(acc);
        }
      }
    });
  }

  deleteECheckAccount(acct: ECheckAccount)
  {
    const dialogData: DialogData = {
      title: 'Confirm Account Deletion',
      message: 'Are you sure you want to delete the selected account?',
      textOkayButton: 'Delete',
      textCancelButton: 'Cancel'
    };

    const dialogRef = this.confirmdialogService.openDialog(dialogData, { disableClose: true });

    dialogRef.afterClosed().subscribe(result =>
    {
      if (result)
      {

        const er = new PostSelectECheckAccountRequest();
        er.RecurringID = acct.RecurringID;
        this.SelectECheckAccountService.Post(er).subscribe(response =>
        {
          if (response.ResponseStatus == "SUCCESS")
          {
            this.snackbarService.showSuccess(response.ResponseMessage);
            this.model.ECheckAccounts = this.model.ECheckAccounts.filter(obj => obj.RecurringID !== er.RecurringID);
          }
          else
          {
            this.snackbarService.showError(response.ResponseMessage);
          }
        });
      } else
      {
      }
    });
  }

  getECheckAccount()
  {

    this.form.get('SelectedECheckAccountDetail').reset();
    const dialogRef = this.echeckDialog.open(ManageECheckAccountComponent, {
      data:
      {
        accountID: this.accountID
      }
    });

    dialogRef.afterClosed().subscribe(result =>
    {
      if (result && result.response.ResponseStatus == "SUCCESS")
      {
        this.model.ECheckAccounts.push(result.response.ECheckAccount);
        this.form.get('SelectedECheckAccountDetail').patchValue(result.response.ECheckAccount);
        this.form.get('SelectedECheckAccountDetail').updateValueAndValidity();
      }
    });
  }

  public onTouched: () => void = () => { };

  writeValue(val: any): void
  {
    val && this.form.setValue(val, { emitEvent: false });
  }

  registerOnChange(fn: any): void
  {
    this.form.valueChanges.subscribe(fn);
  }

  registerOnTouched(fn: any): void
  {
    this.onTouched = fn;
  }

  setDisabledState?(isDisabled: boolean): void
  {
    isDisabled ? this.form.disable() : this.form.enable();
  }

  validate(c: AbstractControl): ValidationErrors | null
  {
    return this.form.valid ? null : { invalidForm: { valid: false, message: "form fields are invalid" } };
  }
}
