import { AfterViewInit, ChangeDetectorRef, Component, EventEmitter, forwardRef, Inject, Input, OnInit, Output } from '@angular/core';
import { AbstractControl, ControlValueAccessor, NG_VALIDATORS, NG_VALUE_ACCESSOR, ValidationErrors, Validator } from '@angular/forms';
import { MatLegacyDialog as MatDialog, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { GetSelectAddressRequest, GetSelectAddressResponse, ISelectAddressService, SelectedAddress, SelectedAddressFormGroup } from '../../@intermediate/select-address';
import { Address } from '../../@intermediate/select-address/models/address.model';
import { ManageAddressComponent } from '../manage-address/manage-address.component';


@Component({
  selector: 'tei-my-account-shared-select-address',
  templateUrl: './select-address.component.html',
  styleUrls: ['./select-address.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => SelectAddressComponent),
      multi: true
    },
    {
      provide: NG_VALIDATORS,
      useExisting: forwardRef(() => SelectAddressComponent),
      multi: true
    }
  ]
})
export class SelectAddressComponent implements OnInit, AfterViewInit, ControlValueAccessor, Validator
{
  model: GetSelectAddressResponse;
  formSelectAddress: SelectedAddressFormGroup;
  fileNameDialogRef: MatDialogRef<ManageAddressComponent>;
  _reload: boolean = false;

  //INPUT PARAMETERS

  @Input() showSaveAddress: boolean = true;

  @Input() public set reload(val: boolean)
  {
    this._reload = val;
    if (this._reload)
    {
      const request = new GetSelectAddressRequest();
      this.load(request);
    }
  }

  // OUTPUT PARAMETERS
  @Output() addedNewAddress = new EventEmitter<Boolean>();

  constructor(@Inject('ISelectAddressService') private SelectAddressService: ISelectAddressService,
    private dialog: MatDialog,
    private _changeDetectorRef: ChangeDetectorRef)
  {

  }

  ngOnInit()
  {
    this.formSelectAddress = new SelectedAddressFormGroup();
    const request = new GetSelectAddressRequest();

    this.load(request);
    this.detectChanges();

    this.formSelectAddress.controls.SelectedAddressDetail.valueChanges.subscribe(x =>
    {
      this.detectChanges();
    });
  }

  ngAfterViewInit()
  {
    this.detectChanges();
  }

  private load(request: GetSelectAddressRequest)
  {
    if (!this.model)
    {
      this.model = new GetSelectAddressResponse();
    }

    this.SelectAddressService.Get(request).subscribe(response =>
    {
      this.model = response;
      const acc: SelectedAddress = { SelectedAddressDetail: this.model.Addresses.find(cc => cc.AddressID === this.formSelectAddress.value.SelectedAddressDetail.AddressID) };
      if (acc && acc.SelectedAddressDetail)
      {
        this.writeValue(acc);
      }
    });
  }

  openAddAddressDialog()
  {
    this.addedNewAddress.emit(false);
    this.fileNameDialogRef = this.dialog.open(ManageAddressComponent, {
      data: {
        showSaveAddress: this.showSaveAddress
      },
      width: '600px',
      panelClass: ['o-dialog-sm-fullscreen']
    });
    this.fileNameDialogRef.afterClosed().subscribe(result =>
    {
      if (result?.AddressID)
      {
        
        if(result.isDisplayed){
          this.model.Addresses.push(result);
          this.formSelectAddress.controls.SelectedAddressDetail.patchValue(result);
          this.addedNewAddress.emit(true);
          this.writeValue(result);
        }
        
      }
    });
  }

  get filterAddressesByIsDisplayedFlag()
  {
    let temp: Array<Address>;
    if (this.model.Addresses)
    {
      temp = this.model.Addresses.filter(x => x.isDisplayed.toLowerCase() === 'true');
    }
    return temp;
  }

  public onTouched: () => void = () => { };

  writeValue(val: any): void
  {
    val && this.formSelectAddress.setValue(val, { emitEvent: false });
  }

  registerOnChange(fn: any): void
  {
    this.formSelectAddress.valueChanges.subscribe(fn);
  }

  registerOnTouched(fn: any): void
  {
    this.onTouched = fn;
  }

  setDisabledState?(isDisabled: boolean): void
  {
    isDisabled ? this.formSelectAddress.disable() : this.formSelectAddress.enable();
  }

  validate(c: AbstractControl): ValidationErrors | null
  {
    return this.formSelectAddress.valid ? null : { invalidForm: { valid: false, message: "form fields are invalid" } };
  }

  detectChanges()
  {
    this._changeDetectorRef.detectChanges();

    setTimeout(() =>
    {
      this._changeDetectorRef.detectChanges();
    }, 100);
  }
}
