import { Inject, Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpParams } from '@angular/common/http';
import { IAPIBaseUrlProvider } from '@tei/core';
import { IViewShippingDetailsService, GetViewShippingDetailsRequest, GetViewShippingDetailsResponse } from '../@intermediate/view-shipping-details';

@Injectable({
  providedIn: 'root'
})

export class ViewShippingDetailsService implements IViewShippingDetailsService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/PaymentEngine/ViewShippingDetails';
  }

  public Get(request: GetViewShippingDetailsRequest): Observable<GetViewShippingDetailsResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetViewShippingDetailsResponse>(apiUrl, { params: request as any });
  }
}
