import { Component, Inject, OnInit } from '@angular/core';
import { MatLegacyDialog as MatDialog, MatLegacyDialogRef as MatDialogRef, MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA } from '@angular/material/legacy-dialog';
import moment from 'moment';
import { GolfCourses, TransactionDetails } from '../../@intermediate/cash-eq-redemption/models/get-cash-eq-redemption.model';
import { VoucherCourseDialogComponent } from '../voucher-course-dialog/voucher-course-dialog.component';



@Component({
  selector: 'tei-payment-engine-shared-voucher-detail-dialog',
  templateUrl: './voucher-detail-dialog.component.html',
  styleUrls: ['./voucher-detail-dialog.component.scss']
})
export class VoucherDetailDialogComponent implements OnInit
{
  Name: string;
  Description: string;
  CashEqID: string;
  CertID: string;
  IssuedDate: Date;
  ExpiryDate: Date;
  OriginalBalance: number;
  CurrentBalance: number;
  IsPromo: boolean;
  CertNumRounds: number;
  MinPlayersRequired: number;
  numPlayers: number;
  CertType: number;
  RedeemableAt: number;
  RemainingRound: number;
  CertificateID: string;
  CourseNames: GolfCourses[];
  Transactions: TransactionDetails[];


  constructor(public dialogRef: MatDialogRef<VoucherDetailDialogComponent>,
    public dialog: MatDialog,
    @Inject(MAT_DIALOG_DATA) public data: any)
  {
    if (data)
    {
      this.Name = data.Name;
      this.Description = data.Description;
      this.CashEqID = data.CashEqID;
      this.CertID = data.CertID;
      this.IssuedDate = data.IssuedDate;
      this.ExpiryDate = this.SetTheExpiryDate(data.ExpiryDate);
      this.OriginalBalance = data.OriginalBalance;
      this.CurrentBalance = data.CurrentBalance;
      this.IsPromo = data.IsPromo;
      this.CertNumRounds = data.CertNumRounds;
      this.MinPlayersRequired = data.MinPlayersRequired;
      this.numPlayers = data.numPlayers;
      this.CertType = data.CertType;
      this.RedeemableAt = data.RedeemableAt;
      this.RemainingRound = data.RemainingRound;
      this.CertificateID = data.CertificateID;
      this.CourseNames = data.CourseNames;
      this.Transactions = data.Transactions;
    }
  }

  ngOnInit(): void
  {
  }

  private SetTheExpiryDate(ExpiryDate: Date): Date
  {
    let expirydate = null;
    if (moment(ExpiryDate).format("YYYY-MM-DD").toString() !== '1900-01-01')
    {
      expirydate = ExpiryDate;
    }
    return expirydate;
  }

  onNoClick(): void
  {
    this.dialogRef.close();
  }

  openDialog()
  {
    this.dialog.open(VoucherCourseDialogComponent, {
      width: '400px',
      height: '600px',
      panelClass:'o-dialog-sm-fullscreen',
      data: {
        Name: this.Name,
        Description: this.Description,
        CashEqID: this.CashEqID,
        CertID: this.CertID,
        IssuedDate: this.IssuedDate,
        ExpiryDate: this.ExpiryDate,
        OriginalBalance: this.OriginalBalance,
        CurrentBalance: this.CurrentBalance,
        IsPromo: this.IsPromo,
        CertNumRounds: this.CertNumRounds,
        MinPlayersRequired: this.MinPlayersRequired,
        numPlayers: this.numPlayers,
        CertType: this.CertType,
        RedeemableAt: this.RedeemableAt,
        RemainingRound: this.RemainingRound,
        CertificateID: this.CertificateID,
        CourseNames: this.CourseNames,
        Transactions: this.Transactions
      },
    });
    this.dialogRef.close();
  }

}
