import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Subject, timer } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'tei-payment-engine-custom-timer',
  templateUrl: './custom-timer.component.html',
  styleUrls: ['./custom-timer.component.scss']
})
export class CustomTimerComponent implements OnInit
{
  @Input() DisplayMessage: string;
  @Input() DisplayEndMessage: string;
  @Input() Duration: number= 0;
  @Output() TimesUp = new EventEmitter<boolean>();

  rxjsTimer = timer(1000, 1000);
  destroy = new Subject();
  timer: number=0;

  constructor()
  {

  }

  //This control accepts the duration left, and calculates the estimated time left
  //Using timer it is able to provide the count down. Once the timer is up it emits a value "true"
  ngOnInit()
  {
    this.rxjsTimer.pipe(takeUntil(this.destroy)).subscribe(val => {
      this.timer = val;

    if (this.timer >= this.Duration) {
      this.destroy.next('');
      this.destroy.complete();
      this.TimesUp.emit(true);
    }
  })
  }

  //property to get the timer displayed on the screen in the format needed
  get display()
  {
    return (Math.floor((this.Duration - this.timer)/60).toString()) + ':' + ('0' + ((this.Duration - this.timer)%60).toString()).slice(-2);
  }

}

