import { Component, EventEmitter, Inject, Input, OnInit, Output } from '@angular/core';
import { LoadingBarService } from '@ngx-loading-bar/core';
import { TranslateService } from '@ngx-translate/core';
import { ScriptService } from '@tei/core';
import { SnackBarService } from '@tei/material';
import
{
  ConvergePayErrorResponse,
  ConvergeResponse,
  GetManageCreditCardRequest,
  GetManageCreditCardResponse,
  IManageCreditCardService,
  PostManageCreditCardRequestForm
} from '../../@intermediate/manage-credit-card';
import { CreditCardDetail } from '../../@intermediate/manage-credit-card/models/creditcarddetail.model';

declare var PayWithConverge: any;

@Component({
  selector: 'tei-payment-engine-shared-manage-credit-card',
  templateUrl: './manage-credit-card.component.html',
  styleUrls: ['./manage-credit-card.component.scss']
})
export class ManageCreditCardComponent implements OnInit
{
  @Input() creditcardID: string;
  @Output() creditCardDetails = new EventEmitter<CreditCardDetail>();

  model: GetManageCreditCardResponse;
  manageCreditCardForm: PostManageCreditCardRequestForm;
  bNotFirstTime: boolean;
  addCreditCard: boolean = false;

  constructor(@Inject('IManageCreditCardService') private ManageCreditCardService: IManageCreditCardService,
    private script: ScriptService,
    private snackbarService: SnackBarService,
    private translateService: TranslateService,
    private loadingBar: LoadingBarService) { }

  public ngOnInit()
  {
    this.loadingBar.start();
    this.manageCreditCardForm = new PostManageCreditCardRequestForm();
    const request = new GetManageCreditCardRequest();
    if (this.addCreditCard)
    {
      request.CreditCardID = "";
      this.model.CreditCard = new CreditCardDetail();
      this.creditCardDetails.emit(this.model.CreditCard);
    }
    else
    {
      request.CreditCardID = this.creditcardID ? this.creditcardID : '';
    }

    this.load(request);
  }

  private load(request: GetManageCreditCardRequest)
  {
    if (!this.model)
    {
      this.model = new GetManageCreditCardResponse();
    }
    this.ManageCreditCardService.Get(request).subscribe(response1 =>
    {
      this.model = response1;
      if (this.model.ConvergeToken !== '') //If this token is provided, it means a call needs to be done to Elavon to capture CC information
      {
        this.script.load(this.model.ConvergeURL).then(data => //Specific call to Elavons JS to popup the screen to capture CC info and react according to what the action is
        {
          const paymentFields = {
            ssl_txn_auth_token: this.model.ConvergeToken
          };
          const callback = {
            onError: (error: any) => //Elavon returns an error
            {
              console.error('ERROR: ' + error);
              this.snackbarService.showError(error);
              this.bNotFirstTime = true;
            },
            onCancelled: () => //CC capture cancelled by user on Elavon data capture screen by clicking cancel
            {
              console.info('CANCELLED');
              this.snackbarService.showInfo(this.translateService.instant('PAYMENT_ENGINE.MANAGE_CREDIT_CARD.ADD_CARD_CANCELLED'));
              this.bNotFirstTime = true;
            },
            onDeclined: (response: ConvergePayErrorResponse) => //Elavon declined adding the credit card
            {
              console.info('DECLINED: ', JSON.stringify(response, null, '\t'));
              this.snackbarService.showInfo(this.translateService.instant('PAYMENT_ENGINE.MANAGE_CREDIT_CARD.DECLINED') + response.errorMessage);
              this.bNotFirstTime = true;
            },
            onApproval: (response: ConvergeResponse) => //Elavon approves adding this credit card
            {
              this.manageCreditCardForm.setValueByConvergeResponse(response);
              this.save(this.manageCreditCardForm);
              this.bNotFirstTime = true;
            }
          };
          this.loadingBar.complete(); //Till the Elavon data capture screen is not open, run our loading bar to avoid user action on the page
          PayWithConverge.open(paymentFields, callback); //Actual call to Elavon with request objects          
        }).catch(error => console.log(error)); //Handling any other errors
      }
      else //If the CC exists in our database
      {
        this.loadingBar.complete();
        this.manageCreditCardForm.setValue(response1);
        this.creditCardDetails.emit(this.model.CreditCard);

      }
    });
  }

  public save(form: PostManageCreditCardRequestForm)
  {
    this.ManageCreditCardService.Post(form.value).subscribe(response =>
    {
      this.creditcardID = response.CreditCard.CreditCardID;
      this.addCreditCard = true;
      this.creditCardDetails.emit(response.CreditCard);
    });
  }

  //If credit card is not valid from Elavon, we display a button the relaunch the Elavon data capture screen for credit card information
  get ShowButton()
  {
    return (
      (this.manageCreditCardForm.controls.CreditCardNumber.value === '' && this.bNotFirstTime)
      || (this.bNotFirstTime && this.addCreditCard)
    );
  }

  //If credit card is valid from Elavon, we display the credit card number and expiry date on the screen
  get ShowFormFields()
  {
    return this.manageCreditCardForm.controls.CreditCardNumber.value.length <= 0;
  }
}
