import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'tei-payment-engine-shared-simple-voucher',
  templateUrl: './simple-voucher.component.html',
  styleUrls: ['./simple-voucher.component.scss']
})
export class SimpleVoucherComponent implements OnInit
{

  @Input('Name') Name: string;
  @Input('Description') Description: string;
  @Input('Amount') Amount: number;

  @Input('CertType') CertType: number;
  @Input('AvailableRounds') AvailableRounds: number;

  @Input('RemainingAmount') RemainingAmount: number;

  @Input('HasApply') HasApply: boolean;
  @Input('IsApplyVisible') IsApplyVisible: boolean;

  @Input('HasDetail') HasDetail: boolean;

  @Input('IsRemoveVisible') IsRemoveVisible: boolean;

  @Input('IsUnavailable') IsUnavailable: boolean;
  @Input('IsUnavailableVisible') IsUnavailableVisible: boolean;

  @Input('HasRemainingAmount') HasRemainingAmount: boolean;
  @Output('OnDetail') OnDetail = new EventEmitter<any>(true);
  @Output('OnApply') OnApply = new EventEmitter<any>(true);
  @Output('OnRemove') OnRemove = new EventEmitter<any>(true);

  constructor() { }

  ngOnInit(): void
  {
  }

  getAvailableRoundsText(): string
  {
    if (this.AvailableRounds === 0)
    {
      return 'No Rounds';
    }

    if (this.AvailableRounds > 1)
    {
      return this.AvailableRounds + ' Rounds';
    }

    return this.AvailableRounds + ' Round';
  }

  onDetailBtnClick() {
    this.OnDetail.emit()
  }
};
