import { Component, OnInit, Inject } from '@angular/core';
import { IViewPickupLocationService, GetViewPickupLocationRequest, GetViewPickupLocationResponse } from '../../@intermediate/view-pickup-location';

@Component({
  selector: 'tei-payment-engine-view-pickup-location',
  templateUrl: './view-pickup-location.component.html',
  styleUrls: ['./view-pickup-location.component.scss']
})
export class ViewPickupLocationComponent implements OnInit
{
  model: GetViewPickupLocationResponse;

  constructor(@Inject('IViewPickupLocationService') private ViewPickupLocationService: IViewPickupLocationService)
  {

  }

  ngOnInit()
  {
    const request = new GetViewPickupLocationRequest();
    this.load(request);
  }

  private load(request: GetViewPickupLocationRequest)
  {
    if (!this.model)
    {
      this.model = new GetViewPickupLocationResponse();
    }

    this.ViewPickupLocationService.Get(request).subscribe(response =>
    {
      this.model = response;
    });
  }

  public navigate()
  {
    if (localStorage.getItem('viewPickupPage') && localStorage.getItem('viewPickupPage').length > 0)
    {
      localStorage.removeItem('viewPickupPage');
    }
    localStorage.setItem('viewPickupPage', window.location.href);
    window.location.href = "/Payment-Engine#/pick-up-location-setting/manage/"; //Navigate to the correct page; MIGHT NEED TO BE CHNAGED, CURRENTLY HARDCODED
  }
}
