import { Inject, Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpParams } from '@angular/common/http';
import { IAPIBaseUrlProvider } from '@tei/core';
import { ISelectECheckAccountService, GetSelectECheckAccountRequest, GetSelectECheckAccountResponse} from '../@intermediate/select-echeck-account';
import { PostSelectECheckAccountRequest, PostSelectECheckAccountResponse } from '../@intermediate/select-echeck-account/models/post-select-echeck-account.model';

@Injectable({
  providedIn: 'root'
})

export class SelectECheckAccountService implements ISelectECheckAccountService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/PaymentEngine/SelectECheckAccount';
  }

  public Get(request: GetSelectECheckAccountRequest): Observable<GetSelectECheckAccountResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetSelectECheckAccountResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostSelectECheckAccountRequest): Observable<PostSelectECheckAccountResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostSelectECheckAccountResponse>(apiUrl, request);
  }


}
