import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { TranslateModule } from '@ngx-translate/core';
import { TeiCurrencyTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { SimpleVoucherComponent } from './simple-voucher.component';


const BASE_MODULES = [CommonModule];
const MATERIAL_MODULES = [MatButtonModule, MatIconModule, MatDividerModule, MatCardModule];
const TEI_MATERIAL_MODULES = [TeiCurrencyTranslateModule, TeiTextTranslatePipeModule, TranslateModule];
const COMPONENTS = [SimpleVoucherComponent];

@NgModule({
  declarations: [
    ...COMPONENTS
  ],
  imports: [
    ...TEI_MATERIAL_MODULES,
    ...MATERIAL_MODULES,
    ...BASE_MODULES
  ],
  exports: [
    ...COMPONENTS
  ]
})
export class SimpleVoucherModule { }
