import { NgModule } from '@angular/core';
import { SelectCreditCardComponent } from './select-credit-card.component';
import { SelectCreditCardService } from '../../@services/select-credit-card.service';
import { FormsModule } from '@angular/forms';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyRadioModule as MatRadioModule } from '@angular/material/legacy-radio';
import { CommonModule } from '@angular/common';
import { ManageCreditCardModule } from '../manage-credit-card/manage-credit-card.module';
import { CreditCardDisplayModule } from '../credit-card-display/credit-card-display.module';
import { NoCreditCardDisplayModule } from '../no-credit-card-display/no-credit-card-display.module';

const BASE_MODULES = [FormsModule, CommonModule];
const MATERIAL_MODULES = [MatRadioModule, MatCardModule ];
const TEI_MATERIAL_MODULES = [ManageCreditCardModule, CreditCardDisplayModule, NoCreditCardDisplayModule];

const COMPONENTS = [SelectCreditCardComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMPONENTS
  ],
  providers: [{ provide: 'ISelectCreditCardService', useClass: SelectCreditCardService }],
})
export class SelectCreditCardModule { }
