import { Component, forwardRef, Inject, OnInit } from '@angular/core';
import { AbstractControl, ControlValueAccessor, NG_VALIDATORS, NG_VALUE_ACCESSOR, ValidationErrors, Validator } from "@angular/forms";
import { CreditCardDetail } from '../../@intermediate/manage-credit-card';
import { GetSelectCreditCardRequest, GetSelectCreditCardResponse, ISelectCreditCardService, SelectedCreditCard, SelectedCreditCardFormGroup } from '../../@intermediate/select-credit-card';


@Component({
  selector: 'tei-payment-engine-shared-select-credit-card',
  templateUrl: './select-credit-card.component.html',
  styleUrls: ['./select-credit-card.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => SelectCreditCardComponent),
      multi: true
    },
    {
      provide: NG_VALIDATORS,
      useExisting: forwardRef(() => SelectCreditCardComponent),
      multi: true
    }
  ],
})
export class SelectCreditCardComponent implements OnInit, ControlValueAccessor, Validator
{
  model: GetSelectCreditCardResponse;
  newCreditCard: CreditCardDetail = new CreditCardDetail();
  public formSelectCreditCard: SelectedCreditCardFormGroup = new SelectedCreditCardFormGroup();

  constructor(@Inject('ISelectCreditCardService') private SelectCreditCardService: ISelectCreditCardService
  )
  { }

  ngOnInit()
  {
    const request = new GetSelectCreditCardRequest();
    this.load(request);
  }

  private load(request: GetSelectCreditCardRequest)
  {

    if (!this.model)
    {
      this.model = new GetSelectCreditCardResponse();
    }

    this.SelectCreditCardService.Get(request).subscribe(response =>
    {
      this.model = response;

      const scc: SelectedCreditCard = { SelectedCreditCardDetail: this.model.CreditCards.find(cc => cc.CreditCardID === this.formSelectCreditCard.value.SelectedCreditCardDetail.CreditCardID) };
      if (scc && scc.SelectedCreditCardDetail)
      {
        this.writeValue(scc);
      }

    });
  }

  //WHat is the selected credit card
  getCreditCard(event: CreditCardDetail)
  {
    this.newCreditCard = event;
    this.newCreditCard.IsNew = true;
    this.formSelectCreditCard.controls.SelectedCreditCardDetail.patchValue(this.newCreditCard);
  }

  public onTouched: () => void = () => { };
  writeValue(val: any): void
  {
    val && this.formSelectCreditCard.setValue(val, { emitEvent: false });
  }
  registerOnChange(fn: any): void
  {
    this.formSelectCreditCard.valueChanges.subscribe(fn);
  }
  registerOnTouched(fn: any): void
  {
    this.onTouched = fn;
  }
  setDisabledState?(isDisabled: boolean): void
  {
    isDisabled ? this.formSelectCreditCard.disable() : this.formSelectCreditCard.enable();
  }
  validate(c: AbstractControl): ValidationErrors | null
  {
    return this.formSelectCreditCard.valid ? null : { invalidForm: { valid: false, message: "No credit card selected" } };
  }
}
