import { Inject, Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpParams } from '@angular/common/http';
import { IAPIBaseUrlProvider } from '@tei/core';
import { IManageCreditCardListService,
        GetManageCreditCardListRequest,
        GetManageCreditCardListResponse,
        PostManageCreditCardListRequest,
        PostManageCreditCardListResponse } from '../@intermediate/manage-credit-card-list';

@Injectable({
  providedIn: 'root'
})

export class ManageCreditCardListService implements IManageCreditCardListService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/PaymentEngine/ManageCreditCardList';
  }

  public Get(request: GetManageCreditCardListRequest): Observable<GetManageCreditCardListResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetManageCreditCardListResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostManageCreditCardListRequest): Observable<PostManageCreditCardListResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostManageCreditCardListResponse>(apiUrl, request);
  }
}
