import { Inject, Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpParams } from '@angular/common/http';
import { IAPIBaseUrlProvider } from '@tei/core';
import { IManageECheckAccountService, GetManageECheckAccountRequest, GetManageECheckAccountResponse, PostManageECheckAccountRequest, PostManageECheckAccountResponse } from '../@intermediate/manage-echeck-account';

@Injectable({
  providedIn: 'root'
})

export class ManageECheckAccountService implements IManageECheckAccountService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/PaymentEngine/ManageECheckAccount';
  }

  public Get(request: GetManageECheckAccountRequest): Observable<GetManageECheckAccountResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetManageECheckAccountResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostManageECheckAccountRequest): Observable<PostManageECheckAccountResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostManageECheckAccountResponse>(apiUrl, request);
  }
}
