import { NgModule } from '@angular/core';
import { ManageCreditCardComponent } from './manage-credit-card.component';
import { ManageCreditCardService } from '../../@services/manage-credit-card.service';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { SnackBarModule } from '@tei/material';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { CommonModule } from '@angular/common';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { TranslateModule } from '@ngx-translate/core';

const CORE_MODULES = [TeiTextTranslatePipeModule,TranslateModule];
const BASE_MODULES = [FormsModule, ReactiveFormsModule, CommonModule];
const MATERIAL_MODULES = [MatButtonModule, MatIconModule, MatInputModule];
const TEI_MATERIAL_MODULES = [SnackBarModule];

const COMPONENTS = [ManageCreditCardComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...CORE_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMPONENTS
  ],
  providers: [{ provide: 'IManageCreditCardService', useClass: ManageCreditCardService }],
})
export class ManageCreditCardModule { }
