import { Component, Inject, OnInit, forwardRef } from '@angular/core';
import { NG_VALIDATORS, NG_VALUE_ACCESSOR } from '@angular/forms';
import { MatExpansionPanel } from '@angular/material/expansion';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { TranslateService } from '@ngx-translate/core';
import { ConfirmDialogService, DialogData, SnackBarService } from '@tei/material';
import { ECheckAccount } from '../../@intermediate/manage-echeck-account';
import { GetManageECheckAccountListRequest, GetManageECheckAccountListResponse, IManageECheckAccountListService } from '../../@intermediate/manage-echeck-account-list';
import { PostSelectECheckAccountRequest } from '../../@intermediate/select-echeck-account';
import { ManageECheckAccountComponent } from '../manage-echeck-account/manage-echeck-account.component';

@Component({
  selector: 'tei-payment-engine-shared-manage-echeck-account-list',
  templateUrl: './manage-echeck-account-list.component.html',
  styleUrls: ['./manage-echeck-account-list.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => ManageECheckAccountListComponent),
      multi: true
    },
    {
      provide: NG_VALIDATORS,
      useExisting: forwardRef(() => ManageECheckAccountListComponent),
      multi: true
    }
  ]
})
export class ManageECheckAccountListComponent implements OnInit
{
  model: GetManageECheckAccountListResponse;

  constructor(@Inject('IManageECheckAccountListService') private ManageECheckAccountListService: IManageECheckAccountListService,
    private echeckDialog: MatDialog,
    private translateService: TranslateService,
    private snackbarService: SnackBarService,
    private confirmdialogService: ConfirmDialogService)
  {

  }

  ngOnInit()
  {
    const request = new GetManageECheckAccountListRequest();
    this.load(request);
  }

  openNewECheckCardPanel(matExpansionPanel: MatExpansionPanel, event: Event)
  {
    event.stopPropagation();
    matExpansionPanel.close();

    const dialogRef = this.echeckDialog.open(ManageECheckAccountComponent, {
      panelClass: 'o-dialog-md-fullscreen',
    });
    dialogRef.afterClosed().subscribe(result =>
    {
      if (result && result.response.ResponseStatus == "SUCCESS" || result.response.ResponseStatus == "RUSSITE")
      {
        this.model.ECheckAccounts.push(result.response.ECheckAccount);
      }
    });
    return false;
  }

  deleteECheckAction(acct: ECheckAccount)
  {
    const dialogData: DialogData = {
      title: this.translateService.instant('PAYMENT_ENGINE.MANAGE_ECHECK_ACCOUNT_LIST.CONFIRM_ACCOUNT_DELETION'),
      message: this.translateService.instant('PAYMENT_ENGINE.MANAGE_ECHECK_ACCOUNT_LIST.ARE_YOU_SURE_YOU_WANT_TO_DELETE_THE_SELECTED_ACCOUNT'),
      textOkayButton: this.translateService.instant('PAYMENT_ENGINE.MANAGE_ECHECK_ACCOUNT_LIST.DELETE'),
      textCancelButton: this.translateService.instant('PAYMENT_ENGINE.MANAGE_ECHECK_ACCOUNT_LIST.CANCEL'),
      buttonColor: 'warn'
    };

    const dialogRef = this.confirmdialogService.openDialog(dialogData, { disableClose: true });

    dialogRef.afterClosed().subscribe(result =>
    {
      if (result)
      {

        const er = new PostSelectECheckAccountRequest();
        er.RecurringID = acct.RecurringID;
        this.ManageECheckAccountListService.Post(er).subscribe(response =>
        {
          if (response.ResponseStatus === "SUCCESS" || response.ResponseStatus === "RUSSITE")
          {
            this.snackbarService.showSuccess(response.ResponseMessage);
            this.model.ECheckAccounts = this.model.ECheckAccounts.filter(obj => obj.RecurringID !== er.RecurringID);
          }
          else
          {
            this.snackbarService.showError(response.ResponseMessage);
          }
        },
          error =>
          {
            if (error["error"].Message.startsWith("Message:Pre-authorized payment eCheck"))
            {
              this.snackbarService.showError(this.translateService.instant('PAYMENT_ENGINE.MANAGE_ECHECK_ACCOUNT_LIST.THIS_ACCOUNT_CANNOT_BE_DELETED_AS_ITS_CURRENTLY_IN_USE'));
            }
            else
            {
              this.snackbarService.showError(error["error"].Message);
            }
          });
      } else
      {
      }
    });

  }

  private load(request: GetManageECheckAccountListRequest)
  {
    if (!this.model)
    {
      this.model = new GetManageECheckAccountListResponse();
    }

    this.ManageECheckAccountListService.Get(request).subscribe(response =>
    {
      this.model = response;

    });
  }

}
