import { Inject, Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpParams } from '@angular/common/http';
import { IAPIBaseUrlProvider } from '@tei/core';
import { IManageCreditCardService, GetManageCreditCardRequest, GetManageCreditCardResponse, PostManageCreditCardRequest, PostManageCreditCardResponse } from '../@intermediate/manage-credit-card';

@Injectable({
  providedIn: 'root'
})

export class ManageCreditCardService implements IManageCreditCardService
{
  private serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/PaymentEngine/ManageCreditCard';
  }

  public Get(request: GetManageCreditCardRequest): Observable<GetManageCreditCardResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetManageCreditCardResponse>(apiUrl, { params: request as any, headers: { ignoreLoadingBar:''}});
  }

  public Post(request: PostManageCreditCardRequest): Observable<PostManageCreditCardResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostManageCreditCardResponse>(apiUrl, request);
  }
}
