import { NgModule } from '@angular/core';
import { ManageCreditCardListComponent } from './manage-credit-card-list.component';
import { ManageCreditCardListService } from '../../@services/manage-credit-card-list.service';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatExpansionModule } from '@angular/material/expansion';
import { MatIconModule } from '@angular/material/icon';
import { SnackBarModule, ConfirmDialogModule } from '@tei/material';
import { CreditCardDisplayModule } from '../credit-card-display/credit-card-display.module';
import { NoCreditCardDisplayModule } from '../no-credit-card-display/no-credit-card-display.module';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { TranslateModule } from '@ngx-translate/core';

const CORE_MODULES = [TeiTextTranslatePipeModule, TranslateModule];
const BASE_MODULES = [FormsModule, ReactiveFormsModule, CommonModule];
const MATERIAL_MODULES = [MatExpansionModule, MatButtonModule, MatIconModule, MatCardModule ];
const TEI_MATERIAL_MODULES = [SnackBarModule, ConfirmDialogModule, CreditCardDisplayModule, NoCreditCardDisplayModule] ;

const COMPONENTS = [ManageCreditCardListComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...CORE_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMPONENTS
  ],
  providers: [{ provide: 'IManageCreditCardListService', useClass: ManageCreditCardListService }],
})
export class ManageCreditCardListModule { }
