import { Component, OnInit, Inject } from '@angular/core';
import { IViewShippingDetailsService, GetViewShippingDetailsRequest, GetViewShippingDetailsResponse } from '../../@intermediate/view-shipping-details';
import { windowToggle } from 'rxjs/operators';

@Component({
  selector: 'tei-payment-engine-shared-view-shipping-details',
  templateUrl: './view-shipping-details.component.html',
  styleUrls: ['./view-shipping-details.component.scss']
})
export class ViewShippingDetailsComponent implements OnInit
{
  model: GetViewShippingDetailsResponse;

  constructor(@Inject('IViewShippingDetailsService') private ViewShippingDetailsService: IViewShippingDetailsService)
  {

  }

  ngOnInit()
  {
    const request = new GetViewShippingDetailsRequest();
    this.load(request);
  }

  private load(request: GetViewShippingDetailsRequest)
  {
    if (!this.model)
    {
      this.model = new GetViewShippingDetailsResponse();
    }

    this.ViewShippingDetailsService.Get(request).subscribe(response =>
    {
      this.model = response;
    });
  }

  public navigate(str: string)
  {
    if (localStorage.getItem('viewShippingPage') && localStorage.getItem('viewShippingPage').length > 0)
    {
      localStorage.removeItem('viewShippingPage');
    }
    localStorage.setItem('viewShippingPage', window.location.href);
    window.location.href = "/Payment-Engine#/shipping-zone/manage/" + str; //Navigate to the correct page; MIGHT NEED TO BE CHNAGED, CURRENTLY HARDCODED
  }
}
