import { Address } from './address.model';
import { UntypedFormGroup, UntypedFormControl, Validators } from '@angular/forms';

export class GetSelectAddressRequest
{ }

export class GetSelectAddressResponse
{
  public Addresses: Array<Address>;
}
export class SelectedAddress
{
  public SelectedAddressDetail: Address;
}

export class SelectedAddressFormGroup extends UntypedFormGroup
{
  constructor()
  {
    super({
      SelectedAddressDetail: new UntypedFormControl("", [Validators.required]),
    });
  }

  public value: SelectedAddress;
}
