import { Component, Inject, OnInit } from '@angular/core';
import { MatLegacyDialog as MatDialog, MatLegacyDialogRef as MatDialogRef, MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA } from '@angular/material/legacy-dialog';
import { ManageLoadingService, ResponsiveService } from '@tei/core';
import { SnackBarService } from '@tei/material';
import { finalize } from 'rxjs';
import { GetManageECheckAccountRequest, GetManageECheckAccountResponse, IManageECheckAccountService, PostManageECheckAccountRequestForm } from '../../@intermediate/manage-echeck-account';

@Component({
  selector: 'tei-payment-engine-shared-manage-echeck-account',
  templateUrl: './manage-echeck-account.component.html',
  styleUrls: ['./manage-echeck-account.component.scss']
})
export class ManageECheckAccountComponent implements OnInit
{
  model: GetManageECheckAccountResponse;
  manageECheckAccountForm: PostManageECheckAccountRequestForm;
  isLoading = false;
  isMobileMode = false;

  constructor(@Inject('IManageECheckAccountService') private ManageECheckAccountService: IManageECheckAccountService,
    private snackbarService: SnackBarService,
    public responsiveService: ResponsiveService,
    public manageLoadingService: ManageLoadingService,
    public dialogRef: MatDialogRef<ManageECheckAccountComponent>,
    @Inject(MAT_DIALOG_DATA) public data: { accountID: string; }
  )
  {

  }

  ngOnInit()
  {
    this.manageECheckAccountForm = new PostManageECheckAccountRequestForm();
    const request = new GetManageECheckAccountRequest();

    this.load(request);
    this.isMobileMode = ['xs', 'sm'].includes(this.responsiveService.breakPoint);
  }

  private load(request: GetManageECheckAccountRequest)
  {
    if (!this.model)
    {
      this.model = new GetManageECheckAccountResponse();
    }

    this.ManageECheckAccountService.Get(request).subscribe(response =>
    {
      this.model = response;
    });
  }

  public save(form: PostManageECheckAccountRequestForm)
  {

    const accountID = this.data?.accountID;

    if (accountID)
    {
      form.value.AccountID = accountID;
    }

    if (this.manageLoadingService.isLoading)
    {
      return;
    }
    this.manageLoadingService.startLoading();
    this.ManageECheckAccountService.Post(form.value).pipe(
      finalize(() => this.manageLoadingService.finishLoading())
    ).subscribe(response =>
    {
      if (response.ResponseStatus === "SUCCESS" || response.ResponseStatus === "RUSSITE")
      {
        this.snackbarService.showSuccess(response.ResponseStatus + ": " + response.ResponseMessage);
        this.dialogRef.close({
          response
        });
      }
      else
      {
        this.snackbarService.showError(response.ResponseStatus + ": " + response.ResponseMessage);
      }
    });
  }

  close(): void
  {
    this.dialogRef.close();
  }

  // accountTypeChange(event: any)
  // {
  //   if (event.value === "Personal")
  //   {
  //     this.manageECheckAccountForm.get("CompanyName").clearValidators();
  //   }
  //   else
  //   {
  //     this.manageECheckAccountForm.get("CompanyName").addValidators(Validators.required);
  //   }
  //   this.manageECheckAccountForm.controls['CompanyName'].updateValueAndValidity();
  // }
}

