import { Inject, Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpParams } from '@angular/common/http';
import { IAPIBaseUrlProvider } from '@tei/core';
import { IManageECheckAccountListService, GetManageECheckAccountListRequest, GetManageECheckAccountListResponse, PostManageECheckAccountListRequest, PostManageECheckAccountListResponse } from '../@intermediate/manage-echeck-account-list';

@Injectable({
  providedIn: 'root'
})

export class ManageECheckAccountListService implements IManageECheckAccountListService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/PaymentEngine/ManageECheckAccountList';
  }

  public Get(request: GetManageECheckAccountListRequest): Observable<GetManageECheckAccountListResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetManageECheckAccountListResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostManageECheckAccountListRequest): Observable<PostManageECheckAccountListResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostManageECheckAccountListResponse>(apiUrl, request);
  }
}
