import { NgModule } from '@angular/core';
import { SelectAddressComponent } from './select-address.component';
import { SelectAddressService } from '../../@services/select-address.service';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyRadioModule as MatRadioModule } from '@angular/material/legacy-radio';
import { CommonModule } from '@angular/common';
import { ManageAddressModule } from '../manage-address/manage-address.module';
import { ManageAddressComponent } from '../manage-address/manage-address.component';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';

const CORE_MODULES = [TeiTextTranslatePipeModule];
const BASE_MODULES = [FormsModule, ReactiveFormsModule, CommonModule];
const MATERIAL_MODULES = [MatButtonModule, MatRadioModule, MatCardModule, MatIconModule];
const TEI_MATERIAL_MODULES = [ManageAddressModule];

const COMPONENTS = [SelectAddressComponent];

@NgModule({
    declarations: [...COMPONENTS],
    imports: [
        ...BASE_MODULES,
        ...MATERIAL_MODULES,
        ...TEI_MATERIAL_MODULES,
        ...CORE_MODULES
    ],
    exports: [
        ...BASE_MODULES,
        ...MATERIAL_MODULES,
        ...TEI_MATERIAL_MODULES,
        ...COMPONENTS
    ],
    providers: [{ provide: 'ISelectAddressService', useClass: SelectAddressService }]
})
export class SelectAddressModule { }
