import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { MatDividerModule } from '@angular/material/divider';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatIconModule } from '@angular/material/icon';
import { TranslateModule } from '@ngx-translate/core';
import { TeiCurrencyTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { VoucherCourseDialogComponent } from './voucher-course-dialog.component';

const BASE_MODULES = [CommonModule];
const MATERIAL_MODULES = [MatDividerModule, MatDialogModule, MatFormFieldModule, MatDividerModule, MatButtonModule];
const TEI_MATERIAL_MODULES = [MatIconModule];
const TEI_CORE_MODULES = [TeiCurrencyTranslateModule, TeiTextTranslatePipeModule, TranslateModule];

const COMPONENTS = [VoucherCourseDialogComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMPONENTS
  ]
})
export class VoucherCourseDialogModule { }
