import { NgModule } from '@angular/core';
import { ManageECheckAccountListComponent } from './manage-echeck-account-list.component';
import { ManageECheckAccountListService } from '../../@services/manage-echeck-account-list.service';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { MatExpansionModule } from '@angular/material/expansion';
import { ManageECheckAccountModule } from '../manage-echeck-account/manage-echeck-account.module';
import { CommonModule } from '@angular/common';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { TranslateModule } from '@ngx-translate/core';

const CORE_MODULES = [TeiTextTranslatePipeModule, TranslateModule];
const BASE_MODULES = [FormsModule, ReactiveFormsModule, CommonModule];
const MATERIAL_MODULES = [MatDialogModule, MatExpansionModule];
const TEI_MATERIAL_MODULES = [ManageECheckAccountModule];

const COMPONENTS = [ManageECheckAccountListComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...CORE_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMPONENTS
  ],
  providers: [{ provide: 'IManageECheckAccountListService', useClass: ManageECheckAccountListService }],
})
export class ManageECheckAccountListModule { }
