import { Inject, Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpParams } from '@angular/common/http';
import { IAPIBaseUrlProvider } from '@tei/core';
import { ISelectCreditCardService, GetSelectCreditCardRequest, GetSelectCreditCardResponse } from '../@intermediate/select-credit-card';

@Injectable({
  providedIn: 'root'
})

export class SelectCreditCardService implements ISelectCreditCardService
{
  private serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/PaymentEngine/SelectCreditCard';
  }

  public Get(request: GetSelectCreditCardRequest): Observable<GetSelectCreditCardResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetSelectCreditCardResponse>(apiUrl, { params: request as any });
  }
}
