import { UntypedFormControl, UntypedFormGroup } from '@angular/forms';
import { CreditCardDetail } from './creditcarddetail.model';
import { GetManageCreditCardResponse } from './get-manage-credit-card.model';


export class PostManageCreditCardRequest
{
  CreditCardNumber: any;
  ExpiryDate: any;
  CreditCardType: any;
  UniqueIDToken: any;
}

export class PostManageCreditCardRequestForm extends UntypedFormGroup
{
  constructor()
  {
    super({
      CreditCardNumber: new UntypedFormControl({ value: '', disabled: true }),
      ExpiryDate: new UntypedFormControl({ value: '', disabled: true }),
      CreditCardType: new UntypedFormControl({ value: '', disabled: true }),
      UniqueIDToken: new UntypedFormControl({ value: '', disabled: true }),
    });
  }

  public value: PostManageCreditCardRequest;

  public setValue(value: GetManageCreditCardResponse)
  {
    const request = new PostManageCreditCardRequest();

    if (value.CreditCard)
    {
      request.CreditCardNumber = value.CreditCard.CreditCardNumber;
      request.ExpiryDate = value.CreditCard.ExpiryDate;
      request.CreditCardType = value.CreditCard.CreditCardType;
      request.UniqueIDToken = value.CreditCard.UniqueIDToken;
      super.setValue(request);
    }
  }

  public setValueByConvergeResponse(value: ConvergeResponse)
  {
    const res: GetManageCreditCardResponse = new GetManageCreditCardResponse();

    res.CreditCard = {
      CreditCardNumber: value.ssl_card_number,
      CreditCardType: value.ssl_card_short_description,
      ExpiryDate: value.ssl_exp_date.substring(0, 2) + "/20" + value.ssl_exp_date.substring(2),
      UniqueIDToken: value.ssl_token,
      IsNew: true,
    };

    this.setValue(res);
  }
}

export class PostManageCreditCardResponse
{
  CreditCard: CreditCardDetail;
}

export class ConvergeResponse
{
  public ssl_txn_time: string;
  public ssl_account_balance: string;
  public ssl_exp_date: string;
  public ssl_transaction_type: string;
  public ssl_card_number: string;
  public ssl_card_short_description: string;
  public ssl_result: string;
  public ssl_token_response: string;
  public ssl_card_type: string;
  public ssl_token: string;
  public ssl_add_token_response: string;
}

export class ConvergePayErrorResponse
{
  errorName: string;
  errorMessage: string;
  errorCode: string;
}