import { NgModule } from '@angular/core';
import { ViewShippingDetailsComponent } from './view-shipping-details.component';
import { ViewShippingDetailsService } from '../../@services/view-shipping-details.service';
import { CommonModule } from '@angular/common';
import { MatIconModule } from '@angular/material/icon';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';

const CORE_MODULES = [TeiTextTranslatePipeModule];
const BASE_MODULES = [CommonModule];
const MATERIAL_MODULES = [MatIconModule];
const TEI_MATERIAL_MODULES = [];

const COMPONENTS = [ViewShippingDetailsComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...CORE_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMPONENTS
  ],
  providers: [{ provide: 'IViewShippingDetailsService', useClass: ViewShippingDetailsService }],
})
export class ViewShippingDetailsModule { }
