import { NgModule } from '@angular/core';
import { CustomTimerComponent } from './custom-timer.component';

const BASE_MODULES = [];
const MATERIAL_MODULES = [];
const TEI_MATERIAL_MODULES = [];

const COMPONENTS = [CustomTimerComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMPONENTS
  ]
})
export class CustomTimerModule { }
