import { Component, EventEmitter, Inject, OnInit, Output } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ConfirmDialogService, DialogData, SnackBarService } from '@tei/material';
import
{
  GetManageCreditCardListRequest,
  GetManageCreditCardListResponse, IManageCreditCardListService, PostManageCreditCardListRequest,
  PostManageCreditCardListRequestForm
} from '../../@intermediate/manage-credit-card-list';
import { CreditCardDetail } from '../../@intermediate/manage-credit-card/models/creditcarddetail.model';

@Component({
  selector: 'tei-payment-engine-shared-manage-credit-card-list',
  templateUrl: './manage-credit-card-list.component.html',
  styleUrls: ['./manage-credit-card-list.component.scss']
})
export class ManageCreditCardListComponent implements OnInit
{
  model: GetManageCreditCardListResponse;
  form: PostManageCreditCardListRequestForm;
  @Output() selectedCCForEdit = new EventEmitter<string>();

  constructor(@Inject('IManageCreditCardListService') private ManageCreditCardListService: IManageCreditCardListService,
    private snackbarService: SnackBarService,
    private translateService: TranslateService,
    private confirmdialogService: ConfirmDialogService)
  {

  }

  ngOnInit()
  {
    this.form = new PostManageCreditCardListRequestForm();
    const request = new GetManageCreditCardListRequest();
    this.load(request);
  }

  private load(request: GetManageCreditCardListRequest)
  {
    if (!this.model)
    {
      this.model = new GetManageCreditCardListResponse();
    }

    this.ManageCreditCardListService.Get(request).subscribe(response =>
    {
      this.model = response;
      this.form.setValue(response);
    });
  }

  save(req: PostManageCreditCardListRequest)
  {
    this.ManageCreditCardListService.Post(req).subscribe(
      response =>
      {
        this.load(new GetManageCreditCardListRequest());
      },
      error =>
      {
        req.CreditCard.IsDeleted = false;
        if (error["error"].Message.startsWith("Message:Pre-authorized payment credit card")) //Cannot delete a credit card in case it exists for pre authorized statement payments
        {
          this.snackbarService.showError(this.translateService.instant('PAYMENT_ENGINE.MANAGE_CREDIT_CARD_LIST.THIS_ACCOUNT_CANNOT_BE_DELETED_AS_ITS_CURRENTLY_IN_USE'));
        }
        else
        {
          this.snackbarService.showError(error["error"].Message);
        }
      }
    );
  }

  //Delete a credit card
  deleteCreditCardAction(creditCardID: string)
  {
    const dialogData: DialogData = {
      title: this.translateService.instant('PAYMENT_ENGINE.MANAGE_CREDIT_CARD_LIST.DELETE'),
      message: this.translateService.instant('PAYMENT_ENGINE.MANAGE_CREDIT_CARD_LIST.ARE_YOU_SURE_YOU_WANT_TO_DELETE_THIS_PAYMENT_METHOD'),
      textOkayButton: this.translateService.instant('PAYMENT_ENGINE.MANAGE_CREDIT_CARD_LIST.DELETE'),
      textCancelButton: this.translateService.instant('PAYMENT_ENGINE.MANAGE_CREDIT_CARD_LIST.CANCEL'),
      buttonColor: 'warn'
    };
    const dialogRef = this.confirmdialogService.openDialog(dialogData, { disableClose: true });

    dialogRef.afterClosed().subscribe(result =>
    {
      if (result)
      {
        const request = new PostManageCreditCardListRequest();
        request.CreditCard = this.model.CreditCards.filter(x => x.CreditCardID === creditCardID)[0];
        request.CreditCard.IsDeleted = true;
        this.save(request);
      } else
      {
        this.snackbarService.showInfo(this.translateService.instant('PAYMENT_ENGINE.MANAGE_CREDIT_CARD_LIST.USER_CANCELED_THE_DELETE_OF_THE_CREDIT_CARD'));
      }
    });
  }

  //Emit the credit card id that is being edited
  modifyCreditCardAction(creditCardID: string)
  {
    this.selectedCCForEdit.emit(creditCardID);
  }

  //Return all credit cards that are not deleted
  get filterByIsDeleted()
  {
    let temp: Array<CreditCardDetail>;
    if (this.model.CreditCards)
    {
      temp = this.model.CreditCards.filter(x => !x.IsDeleted);
    }
    return temp;
  }

  //Emit if a new credit card is being added
  openNewCreditCardPanel()
  {
    this.selectedCCForEdit.emit('');
  }
}
