import { CommonModule } from "@angular/common";
import { NgModule } from "@angular/core";
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import { MatLegacyButtonModule as MatButtonModule } from "@angular/material/legacy-button";
import { MatLegacyCardModule as MatCardModule } from "@angular/material/legacy-card";
import { MatLegacyCheckboxModule as MatCheckboxModule } from "@angular/material/legacy-checkbox";
import { MatLegacyDialogModule as MatDialogModule } from "@angular/material/legacy-dialog";
import { MatDividerModule } from "@angular/material/divider";
import { MatExpansionModule } from "@angular/material/expansion";
import { MatLegacyFormFieldModule as MatFormFieldModule } from "@angular/material/legacy-form-field";
import { MatIconModule } from "@angular/material/icon";
import { MatLegacyInputModule as MatInputModule } from "@angular/material/legacy-input";
import { MatLegacyRadioModule as MatRadioModule } from "@angular/material/legacy-radio";
import { MatLegacySlideToggleModule as MatSlideToggleModule } from "@angular/material/legacy-slide-toggle";
import { MatLegacyTabsModule as MatTabsModule } from "@angular/material/legacy-tabs";
import { CashEqRedemptionService } from "../../@services/cash-eq-redemption.service";
import { CashEqRedemptionComponent } from "./cash-eq-redemption.component";
import { TeiCurrencyTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { SimpleVoucherModule } from "../simple-voucher/simple-voucher.module";

const CORE_MODULES = [TeiTextTranslatePipeModule, TeiCurrencyTranslateModule];
const BASE_MODULES = [FormsModule, ReactiveFormsModule, CommonModule];
const MATERIAL_MODULES = [
  MatFormFieldModule,
  MatInputModule,
  MatButtonModule,
  MatIconModule,
  MatCardModule,
  MatDividerModule,
  MatExpansionModule,
  MatSlideToggleModule,
  MatCheckboxModule,
  MatTabsModule,
  MatSlideToggleModule,
  MatDialogModule,
  MatRadioModule

];
const TEI_MATERIAL_MODULES = [SimpleVoucherModule];

const COMPONENTS = [CashEqRedemptionComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [...BASE_MODULES, ...MATERIAL_MODULES, ...TEI_MATERIAL_MODULES, ...CORE_MODULES],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMPONENTS,
  ],
  providers: [
    { provide: "ICashEqRedemptionService", useClass: CashEqRedemptionService },
  ],
})
export class CashEqRedemptionModule { }
