import { ECheckAccount } from '../../manage-echeck-account';
import { UntypedFormGroup, UntypedFormControl, Validators } from '@angular/forms';

export class GetSelectECheckAccountRequest
{
  public AccountID: string | null;
}

export class GetSelectECheckAccountResponse
{
  public ECheckAccounts: ECheckAccount[];
}

export class SelectedECheckAccount
{
  public SelectedECheckAccountDetail: ECheckAccount;
}

export class SelectedECheckFormGroup extends UntypedFormGroup
{
  constructor()
  {
    super({
      SelectedECheckAccountDetail: new UntypedFormControl("", [Validators.required]),
    });
  }

  public value: SelectedECheckAccount;
}
