export class GetViewShippingDetailsRequest
{
  public RefID: string;
}

export class GetViewShippingDetailsResponse
{
  public ShippingZones: Array<DisplayShippingZone>;
}

export class DisplayShippingZone
{
  public ZoneID: string;
  public Title: string;
  public RateOptions: Array<DisplayRateOption>;
}

export class DisplayRateOption
{
  public Title: string;
  public Rates: Array<DisplayRate>;
}

export class DisplayRate
{
  public Title: string;
  public SubTitle: string;
}
